.main-header__wrapper {
  width: 320px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 13px 20px 14px 20px;
  align-items: center;
  box-sizing: border-box;
  position: relative;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    min-width: 768px;
    display: flex;
    padding: 39px 34px 55px 30px;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    min-width: 1440px;
    display: flex;
    padding: 55px 110px 42px 110px;
  }
}

.main-header__wrapper-position {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .main-header__logo {
    display: block;
    max-width: 174px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    display: block;
    max-width: 210px;
  }
}
.main-header__logo:hover {
  opacity: 80%;
}
.main-header__logo:active {
  opacity: 60%;
}

.main-header__logo-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .main-header__logo-image {
    left: 153px;
    top: 60px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo-image {
    left: 255px;
    top: 50%;
  }
}

.main-nav {
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    min-width: 436px;
  }
}

@media (max-width: 767px) {
  .main-nav__toggle {
    border: none;
    padding: 0;
    background-color: #ffffff;
    background-image: url("../../images/burger_mobile.png");
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .menu-close {
    border: none;
    padding: 0;
    background-color: #ffffff;
    background-image: url("../../images/close-window_mobile.png");
    width: 24px;
    height: 24px;
    cursor: pointer;
    background-repeat: no-repeat;
  }
}
@media (min-width: 768px) {
  .menu-close {
    display: none;
  }
}

.main-nav__wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
  }
}

@media (max-width: 767px) {
  .wrapper-open {
    width: 320px;
    background-color: #ffffff;
    position: absolute;
    right: -20px;
    top: 42px;
    box-sizing: border-box;
    z-index: 5;
    display: inline-block;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    gap: 33px;
  }
}

@media (min-width: 768px) {
  .site-list__item--active {
    border-bottom: 2px solid #68b738;
    padding-bottom: 10px;
  }
}
@media (min-width: 1440px) {
  .site-list__item--active {
    padding-bottom: 8px;
  }
}

@media (max-width: 767px) {
  .site-list__item {
    width: 320px;
    height: 66px;
    text-align: center;
    position: relative;
  }
}

.site-list__item::before {
  content: "";
  height: 1px;
  width: 320px;
  background-color: #e6e6e6;
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .site-list__item::before {
    display: none;
  }
}

.site-list__item:last-child::after {
  content: "";
  height: 1px;
  width: 320px;
  background-color: #e6e6e6;
  position: absolute;
  bottom: 0;
  left: 0;
}
@media (min-width: 768px) {
  .site-list__item:last-child::after {
    display: none;
  }
}

.site-list__item a {
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  display: flex;
}
@media (max-width: 767px) {
  .site-list__item a {
    width: 320px;
    height: 66px;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .site-list__item a {
    justify-content: center;
    align-items: center;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .site-list__item a {
    font-size: 20px;
  }
}
.site-list__item a:hover {
  opacity: 60%;
}
.site-list__item a:active {
  opacity: 30%;
}

@media (min-width: 1440px) {
  .site-list__general a {
    color: #ffffff;
  }
}
.site-list__general a:hover {
  opacity: 60%;
}
.site-list__general a:active {
  opacity: 30%;
}

@media (min-width: 1440px) {
  .site-list__general:first-child {
    border-bottom: 2px solid #ffffff;
  }
}

